import "../assets/scss/App.scss";
import { connect } from "react-redux";
import { addProductButton } from "../Redux/product.action";

const productLogo = require("./../assets/img/hot_dog_logo.png");

function ProductPage({ addProductButton }) {
  function clickAddButton() {
    addProductButton(true);
  }
  return (
    <header className="productHeader">
      <div className="logoContainer">
        <img src={productLogo.default} alt="logo" className="logoImage" />
        <div className="logoText">Crud</div>
      </div>
      <button className="headerButton" onClick={clickAddButton}>
        Add hot-dog
      </button>
    </header>
  );
}

const mapStateToProps = (state) => {
  return {};
};

const mapDispatchToProps = {
  addProductButton,
};

export default connect(mapStateToProps, mapDispatchToProps)(ProductPage);
