import { createReducer } from "@reduxjs/toolkit";
import {
  addProduct,
  updateProduct,
  deleteProduct,
  addProductButton,
  editProductButton,
} from "./product.action.jsx";

const initialState = {
  products: [
    {
      id: "1",
      name: "Burger Hot-Dog",
      title: "5$",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias unde magni vitae, voluptate, ipsum minus quas, autem necessitatibus nam provident dicta praesentium ut eos nesciunt? Officiis laudantium quae neque accusamus. Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ex hic doloribus, dolore quia maxime cum. Tempore molestiae dolores, deleniti optio a libero perferendis velit, corrupti porro, quidem repellendus officia nam?",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fb/Hotdog_-_Evan_Swigart.jpg/1200px-Hotdog_-_Evan_Swigart.jpg",
    },
    {
      id: "2",
      name: "Burger Hot-Dog",
      title: "5$",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias unde magni vitae, voluptate, ipsum minus quas, autem necessitatibus nam provident dicta praesentium ut eos nesciunt? Officiis laudantium quae neque accusamus.",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fb/Hotdog_-_Evan_Swigart.jpg/1200px-Hotdog_-_Evan_Swigart.jpg",
    },
    {
      id: "3",
      name: "Burger Hot-Dog",
      title: "5$",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias unde magni vitae, voluptate, ipsum minus quas, autem necessitatibus nam provident dicta praesentium ut eos nesciunt? Officiis laudantium quae neque accusamus.",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fb/Hotdog_-_Evan_Swigart.jpg/1200px-Hotdog_-_Evan_Swigart.jpg",
    },
    {
      id: "4",
      name: "Burger Hot-Dog",
      title: "5$",
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias unde magni vitae, voluptate, ipsum minus quas, autem necessitatibus nam provident dicta praesentium ut eos nesciunt? Officiis laudantium quae neque accusamus.",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/thumb/f/fb/Hotdog_-_Evan_Swigart.jpg/1200px-Hotdog_-_Evan_Swigart.jpg",
    },
  ],
  status: {
    addProductButton: false,
    editProductButton: false,
  },
};

const productData = createReducer(initialState, {
  [addProduct]: (state, action) => ({
    products: [...state.products, action.payload],
    status: state.status,
  }),
  [updateProduct]: (state, action) => ({
    products: [
      ...state.products.map((product) =>
        product.id === action.payload.id ? action.payload : product,
      ),
    ],
    status: state.status,
  }),
  [deleteProduct]: (state, action) => ({
    products: [
      ...state.products.filter((product) => product.id !== action.payload),
    ],
    status: state.status,
  }),
  [addProductButton]: (state, action) => ({
    products: state.products,
    status: { ...state.status, addProductButton: action.payload },
  }),
  [editProductButton]: (state, action) => ({
    products: state.products,
    status: { ...state.status, editProductButton: action.payload },
  }),
});

export { productData };
