import React, { useState } from "react";
import { connect } from "react-redux";
import { addProductButton, addProduct } from "../Redux/product.action";
import Modal from "react-modal";
import Alert from "@material-ui/lab/Alert";
import "../assets/scss/App.scss";

function AddProductForm({
  addProductButton,
  addProduct,
  statusButton,
  products,
}) {
  const [name, setName] = useState("");
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [image, setImage] = useState("");
  const [alert, setAlert] = useState(false);

  function handleCloseModal() {
    addProductButton(false);
    setAlert(false);
  }
  function handleAddProduct() {
    const newProduct = {
      id: Math.random(),
      name,
      title,
      description,
      image,
    };
    addProduct(newProduct);
    addProductButton(false);
    setName("");
    setTitle("");
    setDescription("");
    setImage("");
    setAlert(false);
  }

  function handleOnChange(e) {
    e.preventDefault();
    let inputValue = e.target.value;
    let inputPlaceholder = e.target.placeholder;
    if (inputPlaceholder === "Name") {
      if (
        products.find(
          (product) => product.name.toLowerCase() === inputValue.toLowerCase(),
        )
      ) {
        return setAlert(true);
      }
      setAlert(false);
      setName(inputValue);
    }
    inputPlaceholder === "Title" && setTitle(inputValue);
    inputPlaceholder === "Description" && setDescription(inputValue);
    inputPlaceholder === "Image" && setImage(inputValue);
  }

  return (
    <Modal
      isOpen={statusButton}
      contentLabel="Add new product"
      className="addProductForm"
    >
      {alert && (
        <Alert severity="warning" className="formWarning">
          This name already exist!
        </Alert>
      )}
      <form onSubmit={handleAddProduct} onChange={handleOnChange}>
        <h2 className="productFormTitle">Add new hot-dog</h2>
        <input type="text" placeholder="Name" className="productFormInput" />
        <br />
        <input type="text" placeholder="Title" className="productFormInput" />
        <br />
        <input
          type="text"
          placeholder="Description"
          className="productFormInput"
        />
        <br />
        <input type="text" placeholder="Image" className="productFormInput" />
        <br />
        <div className="productFormButtonContainer">
          <button onClick={handleCloseModal} className="productFormButton">
            No Thanks
          </button>
          <button type="submit" className="productFormButton">
            Add
          </button>
        </div>
      </form>
    </Modal>
  );
}

const mapStateToProps = (state) => {
  return {
    products: state.productData.products,
    statusButton: state.productData.status.addProductButton,
  };
};

const mapDispatchToProps = {
  addProductButton,
  addProduct,
};

export default connect(mapStateToProps, mapDispatchToProps)(AddProductForm);
