import React, { useState } from "react";
import { connect } from "react-redux";
import { updateProduct, deleteProduct } from "../Redux/product.action";
import Alert from "@material-ui/lab/Alert";
import "../assets/scss/App.scss";

function ProductListItem({ products, product, updateProduct, deleteProduct }) {
  const [showValue, setShowValue] = useState(false);
  const [name, setName] = useState(product.name);
  const [title, setTitle] = useState(product.title);
  const [description, setDescription] = useState(product.description);
  const [image, setImage] = useState(product.image);
  const [alert, setAlert] = useState(false);

  function handleOnClick(e) {
    e.preventDefault();
    if (e.target.value == product.id) setShowValue(true);
  }
  function handleOnInput(e) {
    e.preventDefault();
    let textContent = e.target.textContent;
    let inputId = e.target.id;
    if (inputId === "productName") {
      if (
        products.find(
          (product) => product.name.toLowerCase() === textContent.toLowerCase(),
        )
      ) {
        return setAlert(true);
      }
      setAlert(false);
      setName(textContent);
    }
    inputId === "productTitle" && setTitle(textContent);
    inputId === "productDescription" && setDescription(textContent);
    inputId === "productImageUrl" && setImage(textContent);
  }
  function handleUpgrate() {
    setShowValue(false);
    const upProduct = {
      id: product.id,
      name,
      title,
      description,
      image,
    };
    updateProduct(upProduct);
    setAlert(false);
  }
  function handleDelete() {
    setShowValue(false);
    deleteProduct(product.id);
  }

  return (
    <form onInput={handleOnInput}>
      <img src={product.image} alt={name} className="productImage" />
      {showValue && (
        <p
          id="productImageUrl"
          contentEditable={true}
          suppressContentEditableWarning={true}
          className="productImageUrl"
        >
          {product.image}
        </p>
      )}
      <h2
        contentEditable={showValue}
        suppressContentEditableWarning={true}
        id="productName"
        className="productName"
      >
        {product.name}
      </h2>
      {alert && (
        <Alert severity="warning" className="formWarning">
          This name already exist!
        </Alert>
      )}
      <h4
        contentEditable={showValue}
        suppressContentEditableWarning={true}
        id="productTitle"
      >
        {product.title}
      </h4>
      <p
        className="productDescription"
        id="productDescription"
        contentEditable={showValue}
        suppressContentEditableWarning={true}
      >
        {product.description}
      </p>
      {!showValue ? (
        <button
          type="button"
          value={product.id}
          className="productButton"
          onClick={(e) => handleOnClick(e)}
        >
          Edit
        </button>
      ) : (
        <div>
          <button
            onClick={handleUpgrate}
            className="productUpgrateButton"
            type="button"
          >
            Upgrate
          </button>
          <button
            onClick={handleDelete}
            className="productDeleteButton"
            type="button"
          >
            Delete
          </button>
        </div>
      )}
    </form>
  );
}

const mapStateToProps = (state) => {
  return {
    products: state.productData.products,
  };
};

const mapDispatchToProps = {
  updateProduct,
  deleteProduct,
};

export default connect(mapStateToProps, mapDispatchToProps)(ProductListItem);
